.footer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: Inter, Arial, sans-serif;
}

.footer__conteiner{
  width: 89%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__title {
  color: #8B8B8B;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin: 79px 0 20px;
}

@media screen and (max-width: 426px) {
  .footer__title {
    font-size: 12px;
    margin-bottom: 21px;
  }
}

.footer__zone {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-top: 1px solid #424242;
  padding: 20px 0;
}

@media screen and (max-width: 426px) {
  .footer__zone {
    flex-direction: column-reverse;
    align-items: center;
    padding: 30px 0 20px;
  }
}

@media screen and (max-width: 321px) {
  .footer__zone {

  }
}

.footer__copyrigth {
  color: #FFF;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin: 0;
}

@media screen and (max-width: 426px) {
  .footer__copyrigth {
    color: #8B8B8B;
    margin-top: 30px;
    font-size: 12px;
  }
}

.footer__links {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

@media screen and (max-width: 426px) {
  .footer__links {
    flex-direction: column;
    align-items: center;
  }
}


.footer__item {
  margin: 0 20px 0 0;
}

.footer__item:last-child {
  margin: 0;
}

@media screen and (max-width: 426px) {
  .footer__item  {
    margin: 0 0 12px;
  }

  .footer__item:last-child {
    margin: 0;
  }
}

.footer__link {
  color: #FFF;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-decoration: none;
}

@media screen and (max-width: 426px) {
  .footer__link {
    font-size: 12px;
  }
}
